.site-list__item {
  font-size: 18px;
  line-height: 40px;
  padding-top: 12px;
  padding-bottom: 4px;
  margin: auto;
  font-weight: bold;
  text-align: center;
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
.site-list__item:hover {
  background-color: #669ec0;
}
.site-list__item:active {
  background-color: #5496bd;
}
.site-list__item--main {
  padding-top: 6px;
  padding-bottom: 8px;
}
.site-list__item--photo {
  padding-top: 6px;
  padding-bottom: 7px;
}
.site-list__item--form {
  padding-top: 7px;
  padding-bottom: 7px;
}
.site-list__item--html {
  padding-top: 8px;
  padding-bottom: 5px;
}
.site-list__item a:active {
  color: rgba(255, 255, 255, 0.3);
}
.site-list__item:nth-child(4) {
  box-shadow: inset 0 -10px 10px -8px rgba(0, 0, 0, 0.4);
}
.site-list__item--logo {
  border: none;
  padding-top: 0;
  padding-bottom: 0;
  height: 56px;
}
.site-list__item--logo:hover,
.site-list__item--logo:active {
  background-color: #ffffff;
}
.site-list__item a {
  color: #ffffff;
}
@media (min-width: 768px) {
  .site-list__item {
    font-size: 14px;
    font-weight: bold;
    line-height: 26px;
    margin: 0;
    border: none;
    padding-top: 16px;
    flex-basis: 20%;
  }
  .site-list__item--logo {
    padding-top: 0;
  }
  .site-list__item--main,
  .site-list__item--photo,
  .site-list__item--form,
  .site-list__item--html {
    padding-bottom: 5px;
  }
  .site-list__item:hover {
    background-color: #ffffff;
  }
  .site-list__item:active {
    background-color: #ffffff;
  }
  .site-list__item a {
    color: #000000;
  }
  .site-list__item a:hover {
    color: #81b3d2;
  }
  .site-list__item a:active {
    color: #cacaca;
  }
  .site-list__item:nth-child(3) {
    order: 1;
  }
  .site-list__item:nth-child(4) {
    box-shadow: none;
    order: 1;
  }
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
